.add{
    display: grid;
    margin-top: 30px;
    margin-bottom: 30px;
}

.message{
    width: 100%;
    height: 100px;
    padding: 10px;
    border: none;
    background-color: rgba(0, 143, 36, 0.377);
    box-shadow: 0 0 2px #333;
    color: #fff;
    outline: none;
    margin-bottom: 5px;
    resize: none;
    font-size: 18px;
}

.btn{
    background-color: rgb(95, 95, 95);
    color: #fff;
    padding: 10px;
    border: none;
    outline: none;
    cursor: pointer;
    transition: all ease .2s;
    box-shadow: 0 0 2px #333;
    justify-self: end;
}

.btn:hover{
    background-color: rgb(1, 109, 1);
}